/* Mypage.css */

.mypage-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  text-align: center;
  padding-top: 80px; /* 네비게이션 바의 높이에 맞게 조절 */
}

.mypage-container h2 {
  margin-bottom: 20px;
  color: #333;
}

.mypage-container label {
  display: block;
  margin: 10px 0;
  font-size: 1rem;
  color: #555;
}

.mypage-container input {
  width: calc(100% - 20px);
  padding: 10px;
  margin: 5px 0;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.mypage-container button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 1rem;
  border-radius: 4px;
  cursor: pointer;
  margin: 10px;
  transition: background-color 0.3s;
}

.mypage-container button:hover {
  background-color: #0056b3;
}

.mypage-container .cancel-button {
  background-color: #ff4d4d;
}

.mypage-container .cancel-button:hover {
  background-color: #cc0000;
}

.mypage-container .info {
  margin: 20px 0;
  font-size: 1.1rem;
  color: #333;
}
