/* Navbar.css */

.navbar {
  width: 100%;
  background-color: rgba(255, 255, 255, 0.9); /* 투명한 배경 색상 */
  padding: 10px 20px; /* 패딩 조정 */
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000; /* 다른 요소들 위에 표시되도록 설정 */
  backdrop-filter: blur(8px); /* 배경 흐림 효과 */
}

.navbar-container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.navbar-links {
  display: flex;
  gap: 10px;
}

.navbar-link {
  color: black; /* 글자 색상을 검은색으로 설정 */
  text-decoration: none;
  font-size: 0.9rem; /* 글자 크기 조정 */
  padding: 5px 10px; /* 패딩 조정 */
  border-radius: 4px;
  transition: background-color 0.3s ease;
}

.navbar-link:hover {
  background-color: rgba(200, 200, 200, 0.8); /* 호버 시 배경 색상 */
}

.navbar-button {
  color: black; /* 버튼 글자 색상 검은색으로 설정 */
  background-color: transparent; /* 투명한 배경 */
  border: none;
  font-size: 0.9rem; /* 글자 크기 조정 */
  padding: 5px 10px; /* 패딩 조정 */
  cursor: pointer;
  text-decoration: underline; /* 글자만 보이도록 밑줄 추가 */
  transition: color 0.3s ease;
}

.navbar-button:hover {
  color: rgba(0, 0, 0, 0.8); /* 호버 시 글자 색상 */
}
